* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #292524;
  color: #fafaf9;
  font-family: "sono", sans-serif;
  padding: 0px 40px;
}
.hidden {
  display: none !important;
}

.header {
  margin: 40px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
  gap: 15px;
}

h1 {
  font-size: 42px;
  text-transform: uppercase;
  font-family: "coiny", sans-serif;
  margin-top: 7px;
}

.fact {
  font-size: 20px;
  line-height: 1.4;
  margin-bottom: 16px;
  padding: 16px 24px;
  border-radius: 16px;

  background: #44403c;
  display: flex;
  align-items: center;
  gap: 20px;
}

@media (max-width: 1350px) {
  .fact {
    flex-direction: column;
    align-items: flex-end;
    gap: 12px;
  }
}

.logo img {
  width: 68px;
  height: 68px;
}
.container {
  max-width: 1500px;
  margin: 0 auto;
}
.main {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 40px;
}

/*
section {
  overflow: scroll;
}
*/
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
}

.vote-buttons {
  display: flex;
  flex-shrink: 0;
  margin-left: auto;
  gap: 8px;
}

.vote-buttons button {
  border: none;
  background-color: #78716c;
  font-size: 18px;
  padding: 6px 12px;
  border-radius: 100px;
  color: inherit;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.vote-buttons button:hover {
  background-color: #292524;
}

.tag {
  text-transform: uppercase;
  font-size: 14px;
  font-family: "coiny", sans-serif;
  padding-top: 3px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 100px;
}

ul {
  list-style: none;
}

span {
  font-family: "sono", sans-serif;
}

a {
  color: grey;
  text-decoration-line: none;
  margin-left: 12px;
  transition: 0.3s;
}

a:hover {
  color: white;
}
.disputed {
  color: #ef4444;
  font-weight: 600;
  margin-left: 5px;
}

.btn {
  border: none;
  font-family: "coiny", sans-serif;
  line-height: 1;
  text-transform: uppercase;
  font-size: 17px;
  padding: 16px 0 13px;
  background-image: linear-gradient(135deg, #3b82fb, #ef4444, #16a34a, #eab308);
  color: inherit;
  transition: 0.3s;
  cursor: pointer;
}

.btn:hover {
  transform: scale(110%) rotate(-2deg);
}

.btn-large {
  font-size: 20px;
  padding: 20px 32px 17px;
  border-radius: 30px;
}

.btn-all-cat {
  margin-bottom: 32px;
  width: 100%;
  border-radius: 30px;
}

.btn-cat {
  width: 100%;
  background-image: none;
  margin-bottom: 16px;
  border-radius: 30px;
}

.fact-form {
  background-color: #44403c;
  margin-bottom: 40px;
  padding: 16px 32px;
  display: flex;
  align-items: center;
  gap: 15px;
  border-radius: 16px;
}

.fact-form input,
.fact-form select {
  width: 220px;
  background-color: #78716c;
  border: none;
  border-radius: 100px;
  padding: 16px;
  font-size: 18px;
  color: inherit;
  font-family: inherit;
}

.fact-form input::placeholder {
  color: #a8a29e;
}

.fact-form input:first-child {
  flex-grow: 1;
}

.fact-form span {
  font-weight: 600;
  font-size: 18px;
  margin-right: 18px;
}

@media (max-width: 1000px) {
  body {
    padding: 32px 48px;
  }
  .fact-form {
    flex-direction: column;
    align-items: stretch;
  }

  .fact-form input,
  .fact-form select {
    width: auto;
  }
}

@media (max-width: 600px) {
  #root {
    padding: 24px;
  }

  .header {
    flex-direction: column;
    gap: 16px;
  }

  .fact-form {
    padding: 16px;
  }

  .fact {
    padding: 12px 16px;
  }
}
